.cp_swiper {
  position: relative;
  max-height: 160px;
  overflow: hidden;
}
.cp_swiper .swiper_inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  will-change: transform;
}
.cp_swiper .swiper_item {
  position: relative;
  width: 100%;
  max-height: 160px;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  overflow: auto;
  -webkit-transition: -webkit-transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s;
  transition: -webkit-transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s;
  -o-transition: transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s;
  transition: transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s;
  transition: transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s, -webkit-transform 0.35s cubic-bezier(0.15, 0.3, 0.25, 1) 0s;
}
.cp_swiper .swiper_item img {
  width: 100%;
  height: 100%;
}
.cp_swiper .swiper_page {
  position: absolute;
  width: 100%;
  bottom: 5px;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
}
.cp_swiper .swiper_page .page_item {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: inline-block;
  background: #999;
  margin: 0 4px;
}
.cp_swiper .swiper_page .page_item.active {
  background: #fff;
}



