* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body,
html {
  height: 100%;
  background-color: #f4f4f4;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
#root {
  position: relative;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  padding-top: 44px;
  padding-bottom: 50px;
}
.container {
  width: 100%;
}
.cp_icon {
  display: inline-block;
  vertical-align: middle;
}
.view_content {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}



